				"html": "<p>Before submitting an issue try <a href=\"http://forum.freecodecamp.com/t/searching-for-existing-issues-in-github/18390\">Searching for Your Issue on Github</a></p>\n<p>Crafting a good issue will make it much easier for the dev team to replicate and resolve your problem. Follow these steps to do it right:</p>\n<ol>\n<li>\n<p>Go to FreeCodeCamp's <a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/issues\">Github Issues</a> page and click on <code>New Issue</code>.</p>\n</li>\n<li>\n<p><strong>Have a useful title</strong></p>\n<ul>\n<li>\n<p>Write a meaningful title that describes the issue. Some good examples are <code>Logging in from the News and Field Guide pages doesn't redirect properly (using e-mail)</code> and <code>Typo: \"for\" instead of \"while\" loop</code>; bad examples include <code>A bug, HELP!!!11</code> and <code>I found this bug in a Challenge</code>.</p>\n</li>\n<li>\n<p>Keep the title relatively short, the description is for further information. One example is to shorten long Challenge names, so instead of writing <code>Test case bug in 'Challenge: Check Radio Buttons and Checkboxes by Default'</code>, you might want to write <code>Test case bug in 'Radio Buttons' Challenge</code>.</p>\n</li>\n</ul>\n</li>\n<li>\n<p>In the body, <strong>provide a link</strong> to the page on which you encountered this issue.</p>\n</li>\n<li>\n<p><strong>Describe the problem</strong> and <strong>provide steps</strong> so that a developer can try to replicate the issue. Include your operating system and browser version.</p>\n<ul>\n<li>When referencing other issues or pull requests, simply write <span class=\"hashtag\">#issue</span>/pr-number.</li>\n</ul>\n</li>\n<li>\n<p>Paste in any relevant code using proper <a href=\"http://forum.freecodecamp.com/t/markdown-code-formatting/18391\">Code Formatting</a></p>\n</li>\n<li>\n<p><strong>Take a screenshot</strong> of the issue and include it in the post.</p>\n</li>\n<li>\n<p>Click <code>Submit New Issue</code> and you are done! You will be automatically subscribed to notifications for any updates or future comments.</p>\n</li>\n</ol>",
